.students-edit__form {
  margin-bottom: 40px; }

.students-edit__fieldset {
  padding: 25px 22px;
  margin-bottom: 25px;
  border-bottom: 1px solid #e6e6e6;
  border-top: 1px solid #e6e6e6; }
  @media screen and (min-width: 48em) {
    .students-edit__fieldset {
      padding-top: 30px; } }

.students-edit__fieldset-item {
  margin-bottom: 10px; }
  .students-edit__fieldset-item input,
  .students-edit__fieldset-item select, .students-edit__fieldset-item:last-child {
    margin-bottom: 0; }
  .students-edit__fieldset-item select {
    width: 130px; }
  .students-edit__fieldset-item label span {
    color: #b3b3b3; }
  @media screen and (min-width: 48em) {
    .students-edit__fieldset-item {
      display: table;
      width: 100%;
      margin-bottom: 15px; }
      .students-edit__fieldset-item label {
        width: 33%;
        padding-right: 20px;
        display: table-cell;
        vertical-align: middle;
        text-align: right; }
      .students-edit__fieldset-item input {
        width: 350px; }
      .students-edit__fieldset-item input,
      .students-edit__fieldset-item select {
        display: table-cell;
        vertical-align: middle; } }

.students-edit__btn-wrap {
  padding: 0 22px; }
  .students-edit__btn-wrap button[type='submit'],
  .students-edit__btn-wrap input[type='submit'] {
    width: 100%; }
  @media screen and (min-width: 48em) {
    .students-edit__btn-wrap {
      padding: 0;
      width: 350px;
      margin-left: 34%; } }
